.statistics-classification-binary .code-options .code-input {
  clear: both;
}
.statistics-classification-binary .code-options label {
  display: block;
  float: left;
  width: 35%;
  text-align: right;
}
.statistics-classification-binary .code-options label[title] {
  text-decoration: underline;
  text-decoration-style: dotted;
}
.statistics-classification-binary .code-options input,
.statistics-classification-binary .code-options textarea {
  display: block;
  float: right;
  width: 64%;
  font-family: monospace;
}
.statistics-classification-binary .code-options button {
  display: block;
  float: right;
}
